* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Source Sans Pro", sans-serif;
}

img {
    border-radius: 60px;
}

button {
    border: none;
}

html {
    font-size: 62.5%;
}

.main-div {
    min-height: 100vh;
    background: #060822;
    display: flex;
    justify-content: center;
}

.child-div {
    text-align: center;
    margin: 8rem 0;
}

.child-div figure img {
    width: 12rem;
    height: 12rem;
}

.child-div figure figcaption {
    color: #fff;
    font-size: 2.2rem;
    padding-top: 2rem;
    text-transform: capitalize;
}

input {
    min-width: 40rem;
    height: 3.4rem;
    padding: 2rem 1.2rem;
    font-size: 1.8rem;
    line-height: 1.42857143;
    margin-top: 2rem;
    color: rgb(58, 57, 57);
    background-color: #fff;
    background-image: none;
    border: 0.1rem solid #ccc;
    border-radius: 0.4rem;
    -webkit-box-shadow: inset 0 0.1rem 0.1rem rgb(0 0 0 / 8%);
    box-shadow: inset 0 0.1rem 0.1rem rgb(0 0 0 / 8%);
    transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
}

input,
input:focus,
input:active,
input:active:focus {
    border: none;
    padding: 2.3rem 1.5rem;
    outline: none;
}

input {
    -webkit-box-shadow: none;
    box-shadow: none;
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear;
}

input:focus {
    -webkit-box-shadow: 0 0 1.5rem rgba(0, 0, 0, 0.2);
    box-shadow: 0 0 1.5rem rgba(0, 0, 0, 0.2);
}

.todo-btn {
    width: 5rem;
    display: flex;
    justify-content: space-around;
}

.fa {
    margin-left: -2.5rem;
    cursor: pointer;
    pointer-events: auto;
    z-index: 10;
    background-color: #fff;
    background-image: none;
    color: gray;
    -webkit-transition: color 0.15s linear;
    transition: color 0.15s linear;
    font-size: 2rem;
}

.fa:hover {
    color: rgb(47, 214, 122);
}

.fa-plus:before {
    content: "\f067";
    font-size: 1.5rem;
}

.showItems {
    margin-top: 3rem;
    text-align: center;
}

.eachItem {
    padding: 1.2rem 1rem;
    border-radius: 0.5rem;
    margin-bottom: 0.7rem;
    margin-left: 1rem;
    min-width: 40rem;
    word-break: break-word;
    background: rgb(196, 70, 255);
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: all 0.3s;
}

.eachItem:hover {
    /* border: 1px solid rgb(255, 190, 70); */
    background: rgb(18, 18, 18);
    color: rgb(255, 190, 70);
}

.showItems .eachItem h3 {
    padding-left: 1rem;
    font-size: 1.6rem;
    color: #fff;
}

.fa-trash-alt,
.fa-edit {
    cursor: pointer;
    pointer-events: auto;
    z-index: 10;
    color: rgb(251, 251, 251);
    -webkit-transition: color 0.15s linear;
    transition: color 0.15s linear;
    font-size: 2rem;
}

.addItems .fa-edit {
    color: rgb(47, 214, 122);
}

.fa-edit {
    margin-left: -3.5rem;
}

.eachItem:hover .fa-trash-alt {
    color: rgb(214, 47, 61);
}

.eachItem:hover .fa-edit {
    color: rgb(100, 214, 47);
}

.eachItem:hover h3 {
    color: rgb(255, 190, 70);
}

.btn {
    letter-spacing: 0.1em;
    cursor: pointer;
    font-size: 14px;
    font-weight: 400;
    line-height: 45px;
    max-width: 160px;
    position: relative;
    text-decoration: none;
    text-transform: uppercase;
    width: 100%;
    font-size: 2rem;
    font-weight: bold;
}

.btn:hover {
    text-decoration: none;
}

.effect04 {
    --uismLinkDisplay: var(--smLinkDisplay, inline-flex);
    display: var(--uismLinkDisplay);
    color: #000;
    outline: solid 2px #000;
    position: relative;
    transition-duration: 0.4s;
    overflow: hidden;
}

.effect04::before,
.effect04 span {
    margin: 0 auto;
    transition-timing-function: cubic-bezier(0.86, 0, 0.07, 1);
    transition-duration: 0.4s;
}

.effect04:hover {
    background-color: rgb(255, 190, 70);
}

.effect04:hover span {
    -webkit-transform: translateY(-400%) scale(-0.1, 20);
    transform: translateY(-400%) scale(-0.1, 20);
}

.effect04::before {
    content: attr(data-sm-link-text);
    color: #fff;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    -webkit-transform: translateY(500%) scale(-0.1, 20);
    transform: translateY(500%) scale(-0.1, 20);
}

.effect04:hover::before {
    letter-spacing: 0.05em;
    -webkit-transform: translateY(0) scale(1, 1);
    transform: translateY(0) scale(1, 1);
}